import { Component, OnInit, Input } from "@angular/core";
import { Router } from "@angular/router";
import { Scheduler } from "../types";
import { TimetableService } from "../timetable.service";
import {
  FormGroup,
  FormControl,
  FormArray,
  Validators,
  FormBuilder,
} from "@angular/forms";
@Component({
  selector: "app-schedule-form",
  templateUrl: "./schedule-form.component.html",
  styleUrls: ["./schedule-form.component.css"],
})
export class ScheduleFormComponent implements OnInit {
  form: FormGroup;
  y: Scheduler;
  course: Scheduler[];
  isSuccessful: boolean;
  hasError: boolean=false;
  message: string ='';
  constructor(
    private fb: FormBuilder,

    private timetableService: TimetableService
  ) {}

  ngOnInit() {
    this.isSuccessful=false
    this.course=[]
    this.form = this.fb.group({
      sname: ["", Validators.required],
      items: this.fb.array([this.createField()]),
    });
  }

  createField() {
    return this.fb.group({
      subjectCode: ["", Validators.required],
      courseCode: ["", Validators.required],
    });
  }
  get arrayC() {
    return this.form.get("items") as FormArray;
  }

  addNext() {
    (this.form.controls["items"] as FormArray).push(this.createField());
  }
  remove() {
    (this.form.controls["items"] as FormArray).removeAt(this.arrayC.length - 1);
  }

  submit() {
  //   try{
  //     console.log(this.arrayC.value)
  //   this.arrayC.value.forEach((element,index) => {
  //     this.y={
  //       name: this.form.get('sname').value,
  //       subjectCode: element[index].subjectCode,
  //       courseCode: element[index].courseCode

  //     }
  //     this.course.push(this.y)

  //   });
  this.course=[]
  for (let i = 0; i < this.arrayC.length; i++) {
    this.y={
            name: this.form.get('sname').value,
            subjectCode: this.arrayC.value[i].subjectCode,
            courseCode: this.arrayC.value[i].courseCode

        }

  this.course.push(this.y)
      }

  console.log(JSON.stringify(this.course))
    this.timetableService
    .addToSchedule(
      JSON.stringify(this.course)

    )
    .subscribe(
      (res) => {
        if (this.hasError) this.hasError = false;
        this.isSuccessful = true;
        

      },
      (error) => {
        if (this.isSuccessful) this.isSuccessful = false;
        console.log(error);
        this.message = error;
        this.hasError=true;

      }
    );
  }



}

