import { Component, OnInit } from '@angular/core';
import { ScheduleView, ScheduleWC, TimeView } from "../types";
import {Router} from '@angular/router'
import { TimetableService } from "../timetable.service";
import { FormGroup, FormControl, Validators } from "@angular/forms";
@Component({
  selector: 'app-schedule-page',
  templateUrl: './schedule-page.component.html',
  styleUrls: ['./schedule-page.component.css']
})
export class SchedulePageComponent implements OnInit {
  sedule: ScheduleWC[]=[]
  hasLoaded: boolean=false;
  isSuccessful: boolean = false;
  hasError: boolean = false;
  hasError2: boolean = false;
  hasError3: boolean = false;
  deleteOne: boolean= false;
  deleteAll: boolean =false;
  viewTime:boolean=false;
  form: FormGroup;
  form2: FormGroup;
  timeView: ScheduleView[]=[]
  timetables: TimeView[]=[];
  message: string
  constructor(
    private timetableService: TimetableService,
    private router: Router,
  ) { }

  ngOnInit() {
    this.timetableService.getSchedule().subscribe(courses=>this.sedule=courses);
    this.form = new FormGroup({
      name: new FormControl(
        "",
        Validators.compose([Validators.required])
      ),
  })
  this.form2 = new FormGroup({
    delname: new FormControl(
      "",
      Validators.compose([Validators.required])
    ),
})
}

  viewSchedules(){

    console.log(this.sedule)
    this.hasLoaded=true;
  }
  onButtonClicked(){
    this.timetableService
    .getScheduleInfo(
      this.form.get("name").value,

    )
    .subscribe(
      (res) => {
        if (this.hasError) this.hasError = false;
        this.timeView = res;
        this.isSuccessful = true;
      },
      (error) => {
        if (this.isSuccessful) this.isSuccessful = false;
        console.log("error");
        this.message = error;
        this.hasError=true;
      }
    );
}
onButtonClicked2(){
  this.timetableService
  .deleteASchedule(
    this.form2.get("delname").value,

  )
  .subscribe(
    (res) => {
      if (this.hasError) this.hasError2 = false;
        this.deleteOne = true;

    },
    (error) => {
      if (this.isSuccessful) this.deleteOne = false;
      console.log("error");
      this.message = error;
      this.hasError2=true;
    }

  );
}
delete(){
  this.timetableService
  .deleteAllSchedule(


  )
  .subscribe(
    (res) => {
      if (this.hasError) this.hasError3 = false;
      this.deleteAll = true;
    },
    (error) => {
      if (this.isSuccessful) this.deleteAll = false;
      console.log("error");
      this.message = error;
      this.hasError3=true;
    }
  );
}
time(subjectCode: string, courseCode: string){
  this.timetableService.getTimetable(subjectCode,courseCode).subscribe(
    res => this.timetables=res
  )
  this.viewTime=true;
}

  }


