import { Injectable } from "@angular/core";
import { HttpClient, HttpHeaders, HttpErrorResponse } from "@angular/common/http";
import { observable, Observable, throwError } from "rxjs";
import { catchError} from "rxjs/operators";
import { Course, ScheduleView, ScheduleWC, TimeView } from "./types";
import { Time } from '@angular/common';

const httpOptions = {
  headers: new HttpHeaders({
    "Content-Type": "application/json",
  }),
};
@Injectable({
  providedIn: "root",
})
export class TimetableService {

  constructor(private http: HttpClient) {}

  getCourses(): Observable<Course[]> {
    console.log(this.http.get<Course[]>("/api/courses/"));
    return this.http.get<Course[]>("/api/courses/");
  }

  createSchedule(name:string): Observable<any> {
    return this.http
    .post<any>("/api/schedules", { name }, httpOptions)
    .pipe(
      catchError(this.handleError)
    )
  }

  getTimetable(subjC:string, courseC:string): Observable<TimeView[]> {
   return this.http.get<TimeView[]>(`/api/courses/${subjC}/${courseC}`).pipe(
    catchError(this.handleError)
  )

  }
  getTimetable2(subjC:string, courseC:string, component:string): Observable<TimeView[]> {

    return this.http.get<TimeView[]>(`/api/courses/${subjC}/${courseC}/${component}`).pipe(
      catchError(this.handleError)
    );
  }
  addToSchedule(item: string): Observable<any>{
    console.log(item)
    return this.http
    .post<any>("/api/schedules/courses/", item, httpOptions)
    .pipe(
      catchError(this.handleError)
    )
  }
  getSchedule(): Observable<ScheduleWC[]> {

    return this.http.get<ScheduleWC[]>("/api/schedules/");
  }
  getScheduleInfo(subjC:string): Observable<ScheduleView[]> {
    return this.http.get<ScheduleView[]>(`/api/schedules/${subjC}`).pipe(
     catchError(this.handleError)
   )

   }
   deleteASchedule(name:string): Observable<any>{
    return this.http.delete<any>(`/api/schedules/${name}`)
   }
   deleteAllSchedule(): Observable<any>{
    return this.http.delete<any>('/api/schedules/schedules/7692')
   }

  private handleError(error: HttpErrorResponse) {


      return throwError(
      `${error.error.message}`);
  }
}
